import {Component, Input, OnInit, ViewEncapsulation} from '@angular/core';

@Component({
  selector: 'yo-test',
  template: `
    <h1 class="yo-test-component">
      <ng-content *ngIf="show"></ng-content>
    </h1>
  `,
  styles: [`
    .yo-test-component {
      display: block;
      width: 100%;
      height: 200px;
      line-height: 200px;
      text-align: center;
      font-size: 100px;
      color: white;
      background-color: green;
      font-family: 'Source Sans Pro', sans-serif; }
  `],
  encapsulation: ViewEncapsulation.None
})
export class YoTestComponent implements OnInit {
  show: boolean = true;
  timer: any;

  @Input()
  interval: number = 500;

  constructor() { }

  blink() {
    this.timer = setTimeout(() => {
      this.show = !this.show;
      clearTimeout(this.timer);
      this.blink();
    }, this.interval)
  }

  ngOnInit() {
    this.blink();
  }

}
